label {
  font-weight: 600;
}

[type=text],
[type=search],
[type=tel],
[type=date],
[type=email],
[type=number],
[type=month],
[type=week],
[type=password],
textarea {
  appearance: none;
  border: none;
  font-family: Inter, system-ui, sans-serif;
  font-size: 0.875rem;
  padding: 8px 16px;
  height: 40px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.03);
  transition: background-color 0.15s ease-in-out;
}
[type=text]:hover,
[type=search]:hover,
[type=tel]:hover,
[type=date]:hover,
[type=email]:hover,
[type=number]:hover,
[type=month]:hover,
[type=week]:hover,
[type=password]:hover,
textarea:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
[type=text]:focus,
[type=search]:focus,
[type=tel]:focus,
[type=date]:focus,
[type=email]:focus,
[type=number]:focus,
[type=month]:focus,
[type=week]:focus,
[type=password]:focus,
textarea:focus {
  background-color: #fff;
}

textarea {
  height: initial;
}

[type=search] {
  -webkit-appearance: textfield;
}